<template>
  <tas-base-crud :config="config" hide-create hide-delete hide-update hide-filter hide-detail>
    <template v-slot:list-table-action="data">
      <a @click="$children[0].getEvent('update', data.rowData)" class="btn btn-warning btn-sm"> Update Komponen</a>
    </template>
  </tas-base-crud>
</template>

<script>
export default {
  name: 'crud-mapping-upah-bpjs-detail',
  data () {
    return {
      config: {
        title: 'Komponen Upah BPJS',
        model_api: null,
        getter: 'custom/mapping-upah-bpjs',
        setter: 'custom/mapping-upah-bpjs',
        pk_field: 'name',
        filter_api: {},
        custom_api: {},
        permission: {
          create: 'create-mapping-upah-bpjs',
          read: 'view-mapping-upah-bpjs',
          update: 'update-mapping-upah-bpjs',
          delete: 'delete-mapping-upah-bpjs'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'name', label: 'Nama Komponen', methods: { list: true, detail: true, create: true, update: true, filter: false } },
          { id: 'description', label: 'Keterangan', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          { id: 'koefisien', label: 'Koefisien', methods: { list: true, detail: true, create: { type: 'number' }, update: { type: 'number' }, filter: false } },
          {
            id: 'salary_component_category_id',
            label: 'Kategori Komponen Gaji',
            methods: {
              list: { view_data: 'rel_salary_component_category_id' },
              detail: { view_data: 'rel_salary_component_category_id' },
              create: false,
              update: false,
              filter: {
                setter: 'master-salary-component-categories',
                getter: 'master-salary-component-categories',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              }
            }
          },
          {
            id: 'active',
            data: 'active',
            label: 'Status',
            placeholder: null,
            methods: {
              list: {
                order: true,
                class: { 0: 'badge badge-danger', 1: 'badge badge-primary' },
                transform: 'active'
              },
              detail: {
                order: true,
                class: { 0: 'badge badge-danger', 1: 'badge badge-primary' },
                transform: 'active'
              },
              create: {
                view_data: 'active',
                type: 'radio',
                value: '1',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              },
              update: {
                view_data: 'active',
                type: 'radio',
                value: '1',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              },
              filter: {
                view_data: 'active',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              }
            }
          }
        ]
      }
    }
  }
}
</script>



